import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import { Cotizacion } from '../interfaces/cotizacion.model';

import { NgForm } from '@angular/forms';
import { QuotesService } from '../../../../shared/services/quotes.service' 
import { Quotes } from '../../../../shared/models/quotes';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';

declare var toastr: any;
declare var $: any;

@Component({
  selector: 'vex-cotizacion-create-update',
  templateUrl: './cotizacion-create-update.component.html',
  styleUrls: ['./cotizacion-create-update.component.scss']
})
export class CotizacionCreateUpdateComponent implements OnInit {

  cotizaciones: Quotes = new Quotes();
  quotestList: Quotes[];
  quotestObj: Quotes;
  loading = false;

  static id = 100;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;



  constructor(
    @Inject(MAT_DIALOG_DATA)
    public defaults: any,
    private dialogRef: MatDialogRef<CotizacionCreateUpdateComponent>,
    private fb: FormBuilder,   
    private firestore: AngularFirestore,
    private storageService: StorageService,
    private quotessService: QuotesService
    ) {}

  ngOnInit() {
    if(this.storageService.getSession("$key")!=null){
      this.cotizaciones = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as Cotizacion;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({
      $key: newId,
      id: [CotizacionCreateUpdateComponent.id++],
     // imageSrc: this.defaults.imageSrc,
      producto: [this.defaults.producto || ''],
      cliente: [this.defaults.cliente || ''],
      precio: this.defaults.precio || '',
      pedido: this.defaults.pedido || '',
      factura: this.defaults.factura || '',
      descripcion: this.defaults.descripcion || '',
      //pago: this.defaults.pago || '',
      noCotizacion: this.defaults.noCotizacion || '',
      formaPago: this.defaults.formaPago || '',
    });

  }

  save(quotesForm: NgForm) {
    if (this.mode === 'create') {
      this.addQuotes(quotesForm);
    } else if (this.mode === 'update') {
      this.updateCustomers();
    }
  }

  addQuotes(quotesForm: NgForm) {
    this.quotessService.postQuotes(quotesForm.value);
    this.createCotizacion();
    alert("Cotizacion guardado")

  }

  updateCustomers(){
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          id:  this.cotizaciones.id,
          producto: this.cotizaciones.producto,    
          cliente: this.cotizaciones.cliente,
          pedido: this.cotizaciones.pedido,
          precio: this.cotizaciones.precio,
          factura: this.cotizaciones.factura,
          descripcion: this.cotizaciones.descripcion,
          formaPago: this.cotizaciones.formaPago,
          noCotizacion: this.cotizaciones.noCotizacion,
          
        }
  
        const Cotizacion = this.form.value;  
        Cotizacion.id = String(values.id)
        Cotizacion.producto = String(values.producto)
        Cotizacion.cliente = String(values.cliente)
        Cotizacion.pedido = String(values.pedido)
        Cotizacion.precio = Number(values.precio)
        Cotizacion.factura = String(values.factura)
        Cotizacion.descripcion= String(values.descripcion)
        Cotizacion.formaPago = String(values.formaPago)
        Cotizacion.noCotizacion= Number(values.noCotizacion)   
        this.quotessService.updateQuotes(values)
        this.updateCotizacion()
        console.log('print value::'+ JSON.stringify(Cotizacion));
      }else{
        console.log('no entro')
      }
  }
  createCotizacion() {
    const cotizacion = this.form.value;

    if (!cotizacion.imageSrc) {
      cotizacion.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(cotizacion);
  }

  updateCotizacion() {
    const cotizacion = this.form.value;
    cotizacion.id = this.defaults.id;

    this.dialogRef.close(cotizacion);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }
}
